import React from 'react';
import "../CSS/Reservation.css"
import { Select } from 'semantic-ui-react'
import { Button } from 'semantic-ui-react'

const Universit√© = [
  { key: 'af', value: 'af', text: 'Universit√© de Talence' },
  { key: 'ax', value: 'ax', text: 'Universit√© de Pessac' },
  { key: 'al', value: 'al', text: 'Universit√© de Vicoire' },
]

const Batiment = [
  { key: 'af', value: 'af', text: 'A21' },
  { key: 'ax', value: 'ax', text: 'A22' },
  { key: 'al', value: 'al', text: 'A29' },
]

const Salle = [
  { key: 'a', value: '0', text: 'Aucune pr√©f√©rence' },
  { key: 'af', value: '1', text: '101' },
  { key: 'ax', value: '2', text: '102' },
  { key: 'ap', value: '3', text: '103' },
]

const Duree = [
  { key: 'af', value: '1', text: '30 min' },
  { key: 'ax', value: '2', text: '1H' },
  { key: 'ap', value: '3', text: '1H 30 min' },
  { key: 'al', value: '4', text: '2H' },
  { key: 'ar', value: '5', text: '2H 30 min' },
  { key: 'am', value: '6', text: '3H' },
  { key: 'an', value: '7', text: '3H 30 min' },
  { key: 'ao', value: '8', text: '4H' },
]


const Horaire = [
  { key: 'a', value: 'a', text: 'Aucune pr√©f√©rence' },
  { key: 'b', value: 'b', text: '8H00' },
  { key: 'c', value: 'c', text: '8H30' },
  { key: 'd', value: 'd', text: '9H00' },
  { key: 'e', value: 'e', text: '9H30' },
  { key: 'f', value: 'f', text: '10H00' },
  { key: 'g', value: 'g', text: '10H30' },
  { key: 'h', value: 'h', text: '11H00' },
  { key: 'i', value: 'i', text: '11H30' },
  { key: 'j', value: 'j', text: '12H00' },
  { key: 'k', value: 'k', text: '12H30' },
  { key: 'l', value: 'l', text: '13H00' },
  { key: 'm', value: 'm', text: '13H30' },
  { key: 'n', value: 'n', text: '14H00' },
  { key: 'o', value: 'o', text: '14H30' },
  { key: 'p', value: 'p', text: '15H00' },
  { key: 'q', value: 'q', text: '15H30' },
  { key: 'r', value: 'r', text: '16H00' },
  { key: 's', value: 's', text: '16H30' },
  { key: 't', value: 't', text: '17H00' },
  { key: 'u', value: 'u', text: '17H30' },
]



export default class Reservation extends React.Component {

  constructor(props) {
    super(props);
    this.DateChange = this.DateChange.bind(this);
    this.HoraireChange = this.HoraireChange.bind(this);
    this.FacChange = this.FacChange.bind(this);
    this.BatimentChange = this.BatimentChange.bind(this);
    this.SalleChange = this.SalleChange.bind(this);
    this.testErreur = this.testErreur.bind(this)
    this.EnvoieDonnee = this.EnvoieDonnee.bind(this);


    this.state = {
      Error: "erreur",
      DateJour: Date(),
      Date: Date(),
      Horaire: "",
      Fac: "",
      Batiment: "",
      Salle: "",
      Duree: "",
      FindErreur: false,
      profile: props.profile,
      erreurTextDate: "",
      erreurTextHoraire: "",
      erreurTextFac: "",
      erreurTextBatiment: "",
      erreurTextSalle: "",
      erreurTextDuree: "",
      idSalle: "",
    };
  }

  EnvoieDonnee() {
    let data = [
      {
        Date: this.state.Date,
        Horaire: this.state.Horaire,
        Fac: this.state.Fac,
        Batiment: this.state.Batiment,
        nomSalle: this.state.Salle,
        Duree: this.state.Duree,
        idSalle: this.state.idSalle,
      }
    ]

    this.props.history.push({
      pathname: '/u-resa/RechercheEtReservation',
      data: data
    })
  }

  testErreur(event) {
    event.preventDefault()
    let today = new Date()
    let date = today.getFullYear() + "-0"+ parseInt(today.getMonth()+1) +"-"+today.getDate() 
    if(parseInt(today.getMonth()+1)>= 10){
      date = today.getFullYear() + "-"+ parseInt(today.getMonth()+1) +"-"+today.getDate() 
    }
    if (this.state.Date === this.state.DateJour) {
      this.setState({
        Error: "erreurVisible",
        erreurTextDate: "*Champ non renseign√©."
      })
    }
    else if(this.state.Horaire === ""){
      this.setState({
        Error: "erreurVisible",
        erreurTextHoraire: "*Champ non renseign√©."
      })
    }   
    else if( this.state.Duree === ""){
      this.setState({
        Error: "erreurVisible",
        erreurTextDuree: "*Champ non renseign√©."
      })
    }
    else if(this.state.Fac === ""){
      this.setState({
        Error: "erreurVisible",
        erreurTextFac: "*Champ non renseign√©."
      })
    }
    else if(this.state.Batiment === ""){
      this.setState({
        Error: "erreurVisible",
        erreurTextBatiment: "*Champ non renseign√©."
      })
    }
    else if(this.state.Salle === ""){
      this.setState({
        Error: "erreurVisible",
        erreurTextSalle: "*Champ non renseign√©."
      })
    }


    else if(this.state.Date < date){
      this.setState({
      Error: "erreurVisible",
      erreurTextDate: "*Veuillez saisir une date qui n'est pas dans le pass√©."
    })}

    else if (this.state.Horaire === "Aucune pr√©f√©rence" && this.state.Salle === "Aucune pr√©f√©rence") {
      this.setState({
        Error: "erreurVisible",
      })
    }
    else { this.EnvoieDonnee() }
  }

  DateChange(event) {
    this.setState({ 
      Error: "erreur",
      erreurTextDate: "",
      Date: event.target.value 
    });
  }


  HoraireChange(event) {
    this.setState({       
      Error: "erreur",
    erreurTextHoraire: "",
    Horaire: event.target.textContent });
  }

  FacChange(event) {
    this.setState({ 
      Error: "erreur",
      erreurTextFac: "",
      Fac: event.target.textContent });
  }

  BatimentChange(event) {
    this.setState({ 
      Error: "erreur",
      erreurTextBatiment: "",
      Batiment: event.target.textContent });
  }

  SalleChange(event) {

    this.setState({ 
      Error: "erreur",
      erreurTextSalle: "",
      Salle: event.target.textContent });
  }

  render() {

    return <div>
      <section className="container-fluid reserv">
        <div className="TakeReservation">
          <h2 className="h2font"> Rechercher et r√©server une salle </h2>
          <hr className="separator"></hr>
          <form className="reservForm" onSubmit={this.testErreur}>
  <p id="erreur_Date" className={this.state.Error}>{this.state.erreurTextDate}</p>
            <p className="listTitre"> Date de ma r√©servation : </p>
            <input type="date" className="listD" id="start" name="trip-start" onChange={this.DateChange} />
            <p id="erreur_Heure" className={this.state.Error}>{this.state.erreurTextHoraire}</p>
            <p className="listTitre"> Heure de ma r√©servation : </p>
            <Select className="listD" placeholder='Aucune pr√©f√©rence' options={Horaire} onChange={this.HoraireChange} />
            <p id="erreur_Heure" className={this.state.Error}>{this.state.erreurTextDuree}</p>
            <p className="listTitre"> Dur√©e de ma r√©servation : </p>
            <Select className="listD" placeholder='Aucune pr√©f√©rence' options={Duree} onChange={(e, { value }) => this.setState({Error: "erreur",erreurTextHoraire: "",Duree: value })} />
            <p id="erreur_University" className={this.state.Error}>{this.state.erreurTextFac}</p>
            <p className="listTitre"> Mon Universit√© :  </p>
            <Select className="listD" placeholder='S√©lectionnez mon universit√©' options={Universit√©} onChange={this.FacChange} />
            <p id="erreur_Batiment" className={this.state.Error}>{this.state.erreurTextBatiment}</p>
            <p className="listTitre"> Batiment d√©sir√© : </p>
            <Select id="email" name="email" htmlFor="email" className="listD" placeholder='S√©lectionnez le batiment' options={Batiment} onChange={this.BatimentChange} />
            <p id="erreur_Salle" className={this.state.Error}>{this.state.erreurTextSalle}</p>
            <p className="listTitre"> Salle : </p>
            <Select className="listD" placeholder='S√©lectionnez la salle' options={Salle} onChange={(e, { value }) => this.setState({idSalle: value,Error: "erreur",erreurTextSalle: "",Salle: e.target.textContent })} />
            <div className="ReservationBtn">
              <Button primary>V√©rifier les disponibilit√©s</Button>
            </div>
          </form>
        </div>
      </section>
    </div>
  }
}




